<template>
  <div :ref="id" :style="style"></div>
</template>

<script>
import updateData from "@/mixins/updateData";
import color from "@/mixins/color";
export default {
  name: "liquid",
  mixins: [updateData,color],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    }
  },
  watch: {
    style() {
      this.$nextTick(() => {
        this.myChart && this.myChart.resize();
      });
    },
    option: {
      handler() {
        this.myChart && this.updateChart();
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    init() {
      // 判断是否引入echart包
      if (!window.echarts) {
        console.log("未引入echarts！");
        return;
      }
      const main = this.$refs[this.id];
      if (main) {
        this.myChart = window.echarts.init(main, this.theme);
      }
    },
    updateChart() {
      this.myChart.clear();
      const optionData = this.deepClone(this.dataChart);
      const option = {
        series: [
          {
            type: "liquidFill",
            radius: '90%',
            data: optionData,
            color:this.option.barColor.map((d,i,arr)=>this.getColor(i,false,arr)),
            label:{
              formatter: param=>param.name + '\n' + param.value*100+'%',
              fontSize: this.option.fontSize,
              color:this.option.fontColor
            }
          }
        ],
        tooltip: {
          show: true,
          formatter:param=> param.marker+param.name + '\n' + param.value*100+'%'
        }
      };
      this.myChart.resize();
      this.myChart.setOption(option);
    }
  }
};
</script>

