<template>
  <div :ref="id" :style="style"></div>
</template>

<script>
import updateData from "@/mixins/updateData";
export default {
  name: "progressCir",
  mixins: [updateData],
  data() {
    return {
      percent: 0,
      timer:''
    };
  },
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  mounted() {
    this.init();
    this.slide();
  },
  beforeDestroy(){
    clearInterval(this.timer);
  },
  computed: {
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    }
  },
  watch: {
    style() {
      this.$nextTick(() => {
        this.myChart && this.myChart.resize();
      });
    },
    percent: {
      handler(val) {
          this.myChart && this.updateChart();
      },
      deep: true,
      immediate: true
    },
    option:{
      handler(){
        this.myChart && this.updateChart();
      },
      immediate: true,
      deep:true
    }
  },
  methods: {
    init() {
      // 判断是否引入echart包
      if (!window.echarts) {
        console.log("未引入echarts！");
        return;
      }
      const main = this.$refs[this.id];
      if (main) {
        this.myChart = window.echarts.init(main, this.theme);
      }
    },
    getData() {
      return [
        {
          value: this.percent,
          itemStyle: {
            normal: {
              color: "#fb358a",
              shadowBlur: 10,
              shadowColor: "#fb358a"
            }
          }
        },
        {
          value: 100 - this.percent,
          itemStyle: {
            normal: {
              color: "transparent"
            }
          }
        }
      ];
    },
    updateChart() {
      // this.myChart.clear();
      // const optionData = this.deepClone(this.dataChart);
      const option = {
        // backgroundColor: "#333645",
        title: {
          text: this.percent * 1 + "%",
          x: "center",
          y: "center",
          textStyle: {
            color: "#98a0c4",
            fontWeight: "bolder",
            fontSize: 64
          }
        },
        series: [
          {
            type: "pie",
            radius: ["69%", "79%"],
            silent: true,
            label: {
              normal: {
                show: false
              }
            },

            data: [
              {
                value: 1,
                itemStyle: {
                  normal: {
                    color: "#313443",
                    shadowBlur: 10,
                    shadowColor: "#1b1e25"
                  }
                }
              }
            ],

            animation: false
          },

          {
            type: "pie",
            radius: ["69%", "79%"],
            silent: true,
            label: {
              normal: {
                show: false
              }
            },

            data: [
              {
                value: 1,
                itemStyle: {
                  normal: {
                    color: "#313443",
                    shadowBlur: 50,
                    shadowColor: "#1b1e25"
                  }
                }
              }
            ],

            animation: false
          },

          {
            name: "main",
            type: "pie",
            radius: ["80%", "82%"],
            label: {
              normal: {
                show: false
              }
            },
            data: this.getData(),
            animationEasingUpdate: "cubicInOut",
            animationDurationUpdate: 1000
          }
        ]
      };
      this.myChart.resize();
      this.myChart.setOption(option);
    },
    slide() {
      let index = 0;
      this.$nextTick(() => {
        this.timer = setInterval(()=> {
          if (this.percent == 100) {
            this.percent = 100;
          } else {
            index++; 
            this.percent = index;        
          }
        }, 1000);
      });
    }
  }
};
</script>

