export default {
    props: {
        data: {
            type: [Object, String, Array]
        },
        dataType: {
            type: Number,
            default: 0
        },
        dataQuery: {
            type: Object,
            default: () => {
                return {};
            }
        },
        dataMethod: {
            type: String,
            default: "get"
        },
        time: {
            type: Number,
            default: 0
        },
        url: {
            type: String
        },
        disabled: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            key: false,
            dataChart: [],
            checkChart: "",
            dataUrl: this.url || "",
            myChart: ""
        }
    },
    computed: {
        isApi() {
            return this.dataType === 1;
        }
    },
    watch: {
        url: {
            handler(val) {
                this.dataUrl = val || "";
            },
            deep: true,
            immediate: true
        },
        data: {
            handler() {
                this.updateData();
            },
            deep: true,
            immediate: true
        }
    },
    methods: {
        updateData() {
            if (this.key) return;
            this.key = true;
            const callback = () => {
                this.key = false;
                // 动态数据
                if (this.isApi) {
                    const detail = res => {
                        // 处理返回的数据
                        this.dataChart = res.data || {};
                        if (this.myChart) {
                            this.myChart.clear();
                            this.updateChart();
                        }
                    };
                    let params = this.dataQuery;
                    if (!!sessionStorage.getItem("param")) {
                        params.param = sessionStorage.getItem("param");
                    }
                    this.$httpajax[this.dataMethod](
                        this.dataUrl,
                        (() => {
                            if (this.dataMethod === "get") {
                                return {
                                    params: params
                                };
                            } else if (this.dataMethod === "post") {
                                return params;
                            }
                        })()
                    ).then(res => {
                        detail(res);
                    });
                } else {
                    // 静态数据
                    this.dataChart = this.data;
                    if (this.myChart) {
                        this.myChart.clear();
                        this.updateChart();
                    }
                }
            };
            this.$nextTick(() => {
                callback();
                clearInterval(this.checkChart);
                
                if (this.time !== 0 && this.disabled) {
                    this.checkChart = setInterval(() => {
                        callback();
                    }, this.time);
                }
            });
        }
    }
}