<template>
  <div :ref="id" :style="style"></div>
</template>


<script>
import color from "@/mixins/color";
import updateData from "@/mixins/updateData";
export default {
  name: "bubble",
  mixins: [color,updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    },
    switchTheme() {
      return this.vaildData(this.option.switchTheme, false);
    }
  },
  watch: {
    style() {
      this.$nextTick(() => {
        this.myChart && this.myChart.resize();
      });
    },
    option:{
      handler(){
        this.myChart && this.updateChart();
      },
      immediate: true,
      deep:true
    }
  },
  methods: {
    init() {
      // 判断是否引入echart包
      if (!window.echarts) {
        console.log("未引入echarts！");
        return;
      }
      const main = this.$refs[this.id];
      if (main) {
        this.myChart = window.echarts.init(main, this.theme);
      }
    },
    updateChart() {
      this.myChart.clear();
      const optionData = this.deepClone(this.dataChart);
      const option = {
        grid: {
          left: this.option.gridX || 20,
          top: this.option.gridY || 60,
          right: this.option.gridX2 || 20,
          bottom: this.option.gridY2 || 60
        },
        tooltip: {
          formatter: params =>
            `${params.data[3]}<br>${this.option.xAxisName}:${params.data[0]}<br>${this.option.yAxisName}:${params.data[1]}<br>${params.seriesName}:${params.data[2]}`
        },
        legend: {
          show: this.vaildData(this.option.legend, false),
          orient: this.option.legendOrient || "vertical",
          x: this.option.legendPostion || "left",
          top: 0,
          right: this.option.gridX2 || 20,
          textStyle: {
            fontSize: this.option.legendFontSize || 12,
            color: this.option.legendFontColor ||'#fff'
          },
          data: optionData.categories
        },
        xAxis: {
          type: this.option.xAxisType || "value",
          name: this.option.xAxisName || "",
          nameGap: 40,
          nameLocation: this.option.xAxisNameLocation || "end",
          nameTextStyle: {
            fontSize: this.option.xNameFontSize,
            color: this.option.nameColor
          },
          axisLine: {
            lineStyle: {
              color: this.option.lineColor || "#333"
            }
          },
          inverse: this.vaildData(this.option.xAxisInverse, false),
          show: this.vaildData(this.option.xAxisShow, true),
          splitLine: {
            show: this.vaildData(this.option.xAxisSplitLineShow, false)
          },
          axisLabel: {
            interval: this.option.xAxisinterval || "auto",
            rotate: this.option.xAxisRotate || 0,
            textStyle: {
              color: this.option.nameColor || "#333",
              fontSize: this.option.xNameFontSize || 14
            }
          }
        },
        yAxis: {
          type: this.option.yAxisType || "value",
          name: this.option.yAxisName || "",
          nameLocation: this.option.yAxisNameLocation || "end",
          nameGap: 40,
          nameTextStyle: {
            fontSize: this.option.yNameFontSize,
            color: this.option.nameColor
          },
          axisLabel: {
            textStyle: {
              color: this.option.nameColor || "#333",
              fontSize: this.option.yNameFontSize || 14
            }
          },
          axisLine: {
            lineStyle: {
              color: this.option.lineColor || "#333"
            }
          },
          inverse: this.vaildData(this.option.yAxisInverse, false),
          show: this.vaildData(this.option.yAxisShow, true),
          splitLine: {
            show: this.vaildData(this.option.yAxisSplitLineShow, true)
          }
        },
        series: (() => {
          const list = (optionData.series || []).map((ele, index) => {
            return Object.assign(ele, {
              name: ele.name,
              type: ele.type,
              data: ele.data,
              symbolSize: function(data) {
                return Math.sqrt(data[2]) / 2e1;
              },
              label: {
                show: this.vaildData(this.option.labelShow, false), //开启显示
                formatter: params => params.data[3],
                position: "top", //在上方显示
                fontSize: this.option.labelShowFontSize || 14,
                color: this.option.labelShowColor || "#333",
                fontWeight: this.option.labelShowFontWeight || 500
              },
              itemStyle: {
                color:this.getColor(index,false,this.option.barColor)
              }
            });
          });
          return list;
        })()
      };
      this.myChart.resize();
      this.myChart.setOption(option);
    }
  }
};
</script>

