<template>
  <div :ref="id" :style="style"></div>
</template>

<script>
export default {
  name: "map3d",
  props: {
    click: Function,
    dataFormatter: Function,
    titleFormatter: Function,
    labelFormatter: Function,
    clickFormatter: Function,
    formatter: Function,
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    theme: {
      type: String
    },
    animation: {
      type: Boolean,
      default: true
    },
    child: {
      type: Object,
      default: () => {
        return {};
      }
    },
    time: {
      type: Number,
      default: 0
    },
    url: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: true
    },
    dataType: {
      type: Number,
      default: 0
    },
    dataQuery: {
      type: Object,
      default: () => {
        return {};
      }
    },
    homeUrl: {
      type: String
    },
    dataAppend: {
      type: Boolean,
      default: false
    },
    dataMethod: {
      type: String,
      default: "get"
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    data: {
      type: [Object, String, Array]
    },
    component: {
      type: Object,
      default: () => {
        return {};
      }
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      myChart: ""
    };
  },
  computed: {
    fontSize() {
      return this.option.fontSize || 14;
    },
    mapData() {
      return this.option.mapData || "";
    },
    minWidth() {
      const val = this.option.minWidth;
      if (val > this.width) return val;
    },
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    },
    borderWidth() {
      let borderWidth = (this.option.borderWidth<=1)?1:this.option.borderWidth;
      return borderWidth || 3;
    },
    borderColor() {
      return this.option.borderColor || "#389BB7";
    },
    areaColor() {
      return this.option.areaColor || "#0c162f";
    },
    color() {
      return this.option.color || "#fff";
    },
    empAreaColor() {
      return this.option.empAreaColor || "yellow";
    },
    empColor() {
      return this.option.empColor || "#fff";
    }
  },
  mounted() {
    this.init();
    this.updateChart();
  },
  watch: {
    style() {
      this.$nextTick(() => {
        this.myChart && this.myChart.resize();
      });
    },
    option: {
      handler() {
        this.myChart && this.updateChart();
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    init() {
      // 判断是否引入echart包
      if (!window.echarts) {
        console.log("未引入echarts！");
        return;
      }
      const main = this.$refs[this.id];
      if (main) {
        this.myChart = window.echarts.init(main);
      }
    },
    updateChart() {
      this.myChart.clear();
      if (this.mapData === "") return;
      this.$httpajax(this.mapData).then(mapJson => {
        const optionData = this.deepClone(mapJson.data);
        window.echarts.registerMap("map3d", optionData);
        const option = {
            series: [
              {
                type: "map3D",
                map: "map3d",
                shading: "lambert",
                layoutCenter: ["50%", "50%"],
                label: {
                  show: true,
                  textStyle: {
                    color: this.color, //地图初始化区域字体颜色
                    fontSize: this.fontSize
                  }
                },
                itemStyle: {
                  borderWidth: this.borderWidth,
                  borderColor: this.borderColor,
                  color: this.areaColor
                },
                emphasis: {
                  itemStyle: {
                    color: this.empAreaColor
                  },
                  label:{
                    color:this.empColor
                  }
                },
                data: this.data
              }
            ]
        };
        this.myChart.resize();
        this.myChart.setOption(option, true);
      });
    }
  }
};
</script>

