<script>
import updateData from "@/mixins/updateData";
export default {
  name: "logo",
  mixins:[updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    component: {
      type: Object,
      default: () => {
        return {};
      }
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      currentPage: 1,
      blockNum: 10,
      myChart: "",
      checkSlider: "",
      idIndex: 0
    };
  },
  computed: {
    fontSize() {
      return this.option.fontSize || 20;
    },
    banner() {
      return this.option.banner || false;
    },
    bannerTime() {
      return +(this.option.bannerTime) || 0;
    },
    dataArr() {
      let startIndex = (this.currentPage - 1) * this.blockNum;
      return this.dataChart.slice(startIndex, startIndex + this.blockNum) || [];
    },
    minWidth() {
      const val = this.option.minWidth;
      if (val > this.width) return val;
    },
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    },
    pageNum() {
      return Math.ceil(this.dataChart.length / 10);
    }
  },
  mounted() {
    this.addEventListener();
  },
  watch: {
    banner: {
      handler(newVal, oldVal) {
        this.slide();
      },
      deep: true,
      immediate: true
    },
    bannerTime: {
      handler(val) {
        this.slide();
      },
      deep: true,
      immediate: true
    },
    option:{
      handler(){
        this.myChart && this.updateChart();
      },
      immediate: true,
      deep:true
    }
  },
  methods: {
    addEventListener() {
      let d3 = window.d3;
      d3.select("div.logo svg")
        .selectAll("g.module")
        .attr("cursor", "pointer")
        .on("mouseover", function() {
          d3.select(this)
            .transition()
            .duration(100)
            .ease(d3.easeLinear)
            .attr("transform", "translate(0,-10)")
            .select(".cls")
            .style("fill", "#2563c6")
            .style("stroke", "rgb(255,255,255,0.2)");
        })
        .on("mouseout", function() {
          d3.select(this)
            .transition()
            .duration(100)
            .ease(d3.easeLinear)
            .attr("transform", "translate(0,0)")
            .select(".cls")
            .style("fill", "#45a3e9")
            .style("stroke", "none");
        });
    },
    changeLeft() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    changeRight() {
      if (this.currentPage < this.pageNum) {
        this.currentPage++;
      }
    },
    slide() {
      let index = 0;
      let d3 = window.d3;
      this.$nextTick(() => {
        this.checkSlider && this.checkSlider.stop();
        if (this.bannerTime !== 0 && this.banner) {
          this.checkSlider = d3.interval(() => {  
            this.setSlide(index);
            index++;
            if (index === this.blockNum) {
              index = 0;
            }
          }, this.bannerTime);
        }
      });
    },
    setSlide(idIndex) {
      let d3 = window.d3;
      let logoSvg = d3.select("div.logo svg");
      logoSvg
        .selectAll("g.module")
        .transition()
        .duration(this.bannerTime/2)
        .ease(d3.easeLinear)
        .attr("transform", "translate(0,0)")
        .select(".cls")
        .style("fill", "#45a3e9")
        .style("stroke", "none");

      logoSvg
        .select("#b" + idIndex)
        .transition()
        .duration(this.bannerTime/2)
        .ease(d3.easeLinear)
        .style("fill", "#2563c6")
        .attr("transform", "translate(0,-10)")
        .select(".cls")
        .style("fill", "#2563c6")
        .style("stroke", "rgb(255,255,255,0.2)");
    }
  }
};
</script>

