<template>
  <div :ref="id" :style="style">
    <div class="imgDiv" v-if="direction=='up'" :style="style" @click="clickArrow()">
      <div v-if="iconColor=='red'" class="arrow">
        <img class="arrowImg" src="./img/red1.gif" />
        <span class="start">
          <img src="./img/star.png" />
        </span>
      </div>
      <div v-else class="arrow">
        <img class="arrowImg" src="./img/green1.gif" />
        <span class="start3">
          <img src="./img/star.png" />
        </span>
      </div>
    </div>
    <div class="imgDiv" v-else :style="style" @click="clickArrow()">
      <div v-if="iconColor=='red'" class="arrow">
        <img class="arrowImg" src="./img/red2.gif" />
        <span class="start2">
          <img src="./img/star.png" />
        </span>
      </div>
      <div v-else class="arrow">
        <img class="arrowImg" src="./img/green2.gif" />
        <span class="start4">
          <img src="./img/star.png" />
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import updateData from '@/mixins/updateData'
export default {
  name: "arrow",
  mixins: [updateData],
  props: {
    width: {
      type: [Number, String],
      default: 864
    },
    height: {
      type: [Number, String],
      default: 343
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    },
    data: {},
  },
  created() {
    console.log(this.option);
  },
  methods: {
    clickArrow() {
      let url = this.dataChart.url;
      window.parent.postMessage(url, "http://122.114.94.5:8080/");
    }
  },
  computed: {
    direction() {
      return this.option.direction || 'up'
    },
    iconColor() {
      return this.option.iconColor || 'red'
    },
    setSize: function () {
      return this.option.setSize || false
    },
    setheight: function () {
      return this.option.setheight || "864"
    },
    setwidth: function () {
      return this.option.setwidth || "343"
    },
    minWidth() {
      const val = 500;
      if (val > this.width) return val;
    },
    style() {
      return {
        width: this.setSize ? (this.setwidth + "px") : this.setPx(this.minWidth || this.width),
        height: this.setSize ? (this.setheight + "px") : this.setPx(this.height),
        perspective: this.setPx(+this.width * 0.6)
      };
    },
    imgStyle() {
      return {
        width: this.setSize ? (this.setwidth + "px") : this.setPx(this.minWidth || this.width),
        height: this.setSize ? (this.setheight + "px") : auto
      };
    }
  },
  watch: {

  }
};
</script>

