<template>
  <div :ref="id" :style="style" id="marquee" @click="sentMessage">
    <div :style="styleLeft">
      <span :style="spanAnimation">{{this.dataChart.value}}</span>
    </div>
    <div aria-hidden="true" :style="styleRight">
      <span :style="spanAnimation">{{this.dataChart.value}}</span>
    </div>
  </div>
</template>

<script>
import updateData from "@/mixins/updateData";
export default {
  name: "marquee",
  mixins: [updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  methods: {
    sentMessage() {
      console.log("向父页面传递消息:" + this.dataChart.url);
      let url = this.dataChart.url;
      window.parent.postMessage(url, "http://122.114.94.5:8080/");
    }
  },
  computed: {
    fontSize() {
      return this.option.fontSize || 64;
    },
    fontColor() {
      return this.option.color || "white";
    },
    backgroundColorLeft() {
      return this.option.backgroundColorLeft || "#e5233e";
    },
    backgroundColorRight() {
      return this.option.backgroundColorRight || "#b31e31";
    },
    minWidth() {
      const val = this.option.minWidth;
      if (val > this.width) return val;
    },
    parmasUrl() {
      return this.data.url || "";
    },
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height),
        perspective: this.setPx(+this.width * 0.6),
        fontFamily: this.option.fontName || ""
      };
    },
    styleLeft() {
      return {
        fontSize: this.setPx(this.fontSize),
        fontWeight: this.option.fontWeight,
        color: this.fontColor,
        backgroundColor: this.backgroundColorLeft
      };
    },
    styleRight() {
      return {
        fontSize: this.setPx(this.fontSize),
        fontWeight: this.option.fontWeight,
        color: this.fontColor,
        backgroundColor: this.backgroundColorRight
      };
    },
    spanAnimation() {
      return {
        animationDuration: this.option.time + "s"
      }
    }
  }
};
</script>

<style scoped>
#marquee div {
  display: inline-block;
  height: 100%;
  width: 50%;
  position: relative;
}

#marquee div:first-of-type {
  -webkit-transform-origin: top right;
  transform-origin: top right;
  -webkit-transform: rotateY(-40deg);
  transform: rotateY(-40deg);
}

#marquee div:last-of-type {
  -webkit-transform-origin: top left;
  transform-origin: top left;
  -webkit-transform: rotateY(45deg);
  transform: rotateY(45deg);
}

#marquee div {
  overflow: hidden;
}

#marquee div span {
  position: absolute;
  width: 400%;
  line-height: 1.5;
  white-space: nowrap;
}

@-webkit-keyframes leftcrawl {
  to {
    -webkit-transform: translateX(-60%);
    transform: translateX(-60%);
  }
}

@keyframes leftcrawl {
  to {
    -webkit-transform: translateX(-60%);
    transform: translateX(-60%);
  }
}
@-webkit-keyframes rightcrawl {
  to {
    -webkit-transform: translateX(-85%);
    transform: translateX(-85%);
  }
}
@keyframes rightcrawl {
  to {
    -webkit-transform: translateX(-85%);
    transform: translateX(-85%);
  }
}
#marquee div:first-of-type span {
  -webkit-transform: translateX(50%);
  transform: translateX(50%);
  -webkit-animation: leftcrawl 10s linear infinite;
  animation: leftcrawl 10s linear infinite;
  text-shadow: 4px 0px 4px rgba(0, 0, 0, 0.3);
}

#marquee div:last-of-type span {
  -webkit-transform: translateX(25%);
  transform: translateX(25%);
  -webkit-animation: rightcrawl 10s linear infinite;
  animation: rightcrawl 10s linear infinite;
}
</style>