<template>
  <div :ref="id" class="swiperDiv f20" :style="[fontFamily]">
    <div class="swiperDiv-top">
      <div class="swiperDiv-top-title" :style="[thstyle, { width: allWidth() }]">
        <p v-if="rankBol" :style="[styleHead, rankw()]" class="rankList">{{rankName}}</p>
        <p
          v-for="(headItem, index) in columnOption"
          :style="[styleHead, styleWidth(headItem)]"
          :key="index * 1000"
        >{{ headItem.label }}</p>
      </div>
      <div
        class="swiperDiv-top-rank"
        :style="[{ width: allWidth(), height: allHeight2 }]"
        v-show="topNum > 0"
      >
        <div
          class="testTable_td"
          :style="[thstyle]"
          v-for="(fixedItem, num) in topList"
          :key="num"
          @click="tableClick(fixedItem)"
        >
          <p
            v-if="rankBol"
            :style="[rankStyle, rankw()]"
            class="rankList"
            :class="`rankListTop${num+1}`"
          >
            <span>
              {{
              num + 1
              }}
            </span>
          </p>
          <template v-for="(tt, index2) in columnOption">
            <p :style="[style, styleWidth(tt)]" v-if="columnData.includes(tt.prop)" :key="index2">
              <span v-html="fixedItem[tt.prop]"></span>
            </p>
          </template>
        </div>
      </div>
    </div>
    <div
      id="swiper-container"
      class="swiper-container"
      :style="[{ width: allWidth(), height: allHeight }]"
    >
      <div class="swiper-wrapper swiper-no-swiping">
        <div class="swiper-slide" v-for="(items, num2) in groupedArray" :key="num2">
          <div>
            <div
              class="testTable_td"
              :style="[thstyle]"
              v-for="(item, num3) in items"
              :key="num3"
              @click="tableClick(item)"
            >
              <p
                v-if="rankBol"
                :style="[rankStyle, rankw()]"
                class="rankList"
                :class="`rankList${num2 * 3 + 1 + topList.length + num3 }`"
              >
                <span>{{ num2 * pageNum + 1 + topList.length + num3 }}</span>
              </p>
              <template v-for="tt in columnOption">
                <p :style="[style, styleWidth(tt)]" v-if="columnData.includes(tt.prop)">
                  <span v-html="item[tt.prop]"></span>
                </p>
              </template>
            </div>
          </div>
        </div>
      </div>
      <div v-show="scrollPage" class="swiper-button-prev"></div>
      <div v-show="scrollPage" class="swiper-button-next"></div>
    </div>
  </div>
</template>
<script>
import updateData from '@/mixins/updateData'
// import Swiper from 'swiper'
export default {
  name: 'testTable',
  mixins: [updateData],
  props: {
    option: {
      type: Object,
      default: () => {
        return {}
      },
    },
    id: {
      type: String,
      default: 'main_' + new Date().getTime(),
    },
    data: {
      type: Array,
      default: () => {
        return []
      },
    },
    activeOption: Object,
  },
  data() {
    return {
      mySwiper: '',
      columnData: [],
      // columnData: ['type1', 'type2'],
      // columnData: ['type1', 'type2', 'url'],
      list: [],
      groupedArray: [],
      topList: [],
      dataChart: [],
    }
  },
  computed: {
    rankName() {
      return this.option.rankName || '排名'
    },
    scrollDirection() {
      return this.option.scrollDirection || 'up'
    },
    crudOption() {
      return Object.assign(this.option, {
        menu: false,
        align: 'center',
        headerAlign: 'center',
        header: false,
      })
    },
    columnOption() {
      return this.crudOption.column || []
    },
    scrollPage() {
      return this.option.scrollPage || false
    },
    lineHeight() {
      return this.option.rankHeight || 50
    },
    fontSize() {
      return this.option.fontSize || 14
    },
    line() {
      return this.option.line || false
    },
    style() {
      return {
        fontSize: this.setPx(this.fontSize),
        float: 'left',
        lineHeight: this.setPx(this.lineHeight),
        height: this.setPx(this.lineHeight),
        textAlign: this.option.bodyTextAlign || 'center',
        background: this.option.bodyBackgroud || 'rgba(0, 0, 0, 0.01)',
        color: this.option.bodyColor || 'rgba(154, 168, 212, 1)',
        borderWidth: this.line ? '1px' : '0px',
        borderColor: this.option.borderColor || 'rgba(51, 65, 107, 1)',
        borderStyle: 'solid',
        boxSizing: 'border-box',
        overflow: "hidden"

      }
    },
    fontFamily() {
      return {
        fontFamily: this.option.fontName || ""
      }
    },
    rankStyle() {
      return {
        fontSize: this.setPx(this.fontSize),
        float: 'left',
        lineHeight: this.setPx(this.lineHeight),
        height: this.setPx(this.lineHeight),
        textAlign: this.option.rankbodyTextAlign || 'center',
        background: this.option.bodyBackgroud || 'rgba(0, 0, 0, 0.01)',
        color: this.option.rankColor || 'rgba(154, 168, 212, 1)',
        borderWidth: this.line ? '1px' : '0px',
        borderColor: this.option.borderColor || 'rgba(51, 65, 107, 1)',
        borderStyle: 'solid',
        boxSizing: 'border-box',
        overflow: "hidden"
      }
    },
    styleHead() {
      return {
        fontSize: this.setPx(this.fontSize),
        float: 'left',
        lineHeight: this.setPx(this.lineHeight),
        height: this.setPx(this.lineHeight),
        textAlign: this.option.bodyTextAlign || 'center',
        background: this.option.headerBackgroud || 'rgba(0, 0, 0, 0.01)',
        color: this.option.headerColor || 'rgba(154, 168, 212, 1)',
        borderWidth: this.line ? '1px' : '0px',
        borderColor: this.option.borderColor || 'rgba(51, 65, 107, 1)',
        borderStyle: 'solid',
        boxSizing: 'border-box',
        overflow: "hidden"
      }
    },
    thstyle() {
      return {
        height: this.setPx(this.lineHeight),
        fontSize: this.setPx(this.fontSize),
        textAlign: this.option.headerTextAlign || 'center',
        background: this.option.headerBackground || 'rgba(0, 0, 0, 0.01)',
        color: this.option.headerColor || 'rgba(154, 168, 212, 1)',
        overflow: "hidden"
      }
    },
    // allWidth() {
    //   return this.columnData.length * 400 + 'px'
    // },
    allHeight() {
      return this.lineHeight * this.pageNum + 'px'
    },
    allHeight2() {
      return this.lineHeight * this.topNum + 'px'
    },
    scroll() {
      return true
    },
    scrollTime() {
      return this.option.scrollTime || 3000
    },
    pageNum() {
      return this.option.scrollCount || 3
    },
    topNum() {
      return this.option.fixedCount || 0
    },
    allNum() {
      return this.pageNum + this.topNum
    },
    bodyTextAlign() {
      return this.option.bodyTextAlign || 'center'
    },
    rankbodyTextAlign() {
      return this.option.rankbodyTextAlign || 'center'
    },
    bodyColor() {
      return this.option.bodyColor || 'rgba(154, 168, 212, 1)'
    },
    speed() {
      return this.option.speed || 500
    },
    rankWidth() {
      return this.option.rankWidth || 200
    },
    rankBol() {
      return this.option.rankBol || false
    },
    borderColor() {
      return this.option.borderColor || 'rgba(51, 65, 107, 1)'
    },
    pageType() {
      return this.option.pageType || 1
    }
  },
  created() {
    this.columnOption.forEach((ele) => {
      this.columnData.push(ele.prop)
    })

    this.list = this.data
    this.topList = JSON.parse(JSON.stringify(this.list)).slice(0, this.topNum)
    let listArr = JSON.parse(JSON.stringify(this.list)).splice(
      this.topNum,
      this.list.length - this.topNum
    )
    this.groupedArray = this.group(listArr, this.pageNum)
  },
  watch: {
    dataChart() {
      // console.log('数据变化')
      // console.log(this.dataChart)
      // if (!!this.mySwiper.autoplay.stop()) {
      //   this.mySwiper.autoplay.stop()
      // }
      this.list = ''
      this.list = this.dataChart
      this.topList = JSON.parse(JSON.stringify(this.list)).slice(0, this.topNum)
      let listArr = JSON.parse(JSON.stringify(this.list)).splice(
        this.topNum,
        this.list.length - this.topNum
      )
      this.groupedArray = this.group(listArr, this.pageNum)
      this.setSwiper()
    },
    borderColor() {
      this.setSwiper()
    },
    fontSize() {
      this.setSwiper()
    },
    speed() {
      this.setSwiper()
    },
    line() {
      this.setSwiper()
    },
    bodyColor() {
      this.setSwiper()
    },
    scrollDirection() {
      this.setSwiper()
    },
    bodyTextAlign() {
      this.setSwiper()
    },
    rankbodyTextAlign() {
      this.setSwiper()
    },
    rankWidth() {
      this.setSwiper()
    },
    data() {
      // if (!!this.mySwiper.autoplay.stop()) {
      //   this.mySwiper.autoplay.stop()
      // }
      this.list = ''
      this.list = this.data
      this.topList = JSON.parse(JSON.stringify(this.list)).slice(0, this.topNum)
      let listArr = JSON.parse(JSON.stringify(this.list)).splice(
        this.topNum,
        this.list.length - this.topNum
      )
      this.groupedArray = this.group(listArr, this.pageNum)
      this.setSwiper()
    },
    lineHeight() {
      this.setSwiper()
    },
    topNum() {
      this.mySwiper.autoplay.stop()
      this.topList = JSON.parse(JSON.stringify(this.list)).slice(0, this.topNum)
      let listArr = JSON.parse(JSON.stringify(this.list)).splice(
        this.topNum,
        JSON.parse(JSON.stringify(this.list)).length - this.topNum
      )
      this.groupedArray = this.group(listArr, this.pageNum)
      this.setSwiper()
    },
    scrollTime() {
      this.mySwiper.autoplay.stop()
      this.mySwiper.params.autoplay.delay = this.scrollTime
      this.mySwiper.autoplay.start()
    },
    scroll() {
      if (this.scroll) {
        this.mySwiper.params.autoplay.delay = this.scrollTime
        this.mySwiper.autoplay.start()
      } else {
        this.mySwiper.autoplay.stop()
      }
    },
    columnOption() {
      this.columnOption.forEach((ele) => {
        this.columnData.push(ele.prop)
      })
      this.setSwiper()
    },
    pageNum() {
      this.mySwiper.autoplay.stop()
      this.topList = JSON.parse(JSON.stringify(this.list)).slice(0, this.topNum)
      let listArr = JSON.parse(JSON.stringify(this.list)).splice(
        this.topNum,
        JSON.parse(JSON.stringify(this.list)).length - this.topNum
      )
      this.groupedArray = this.group(listArr, this.pageNum)
      this.setSwiper()
    },
  },
  mounted() {
    // this.setSwiper()
    // let _this = this;
    // setTimeout(() => {
    //   var comtainer = document.getElementById('swiper-container');

    // this.mySwiper.el.onmouseenter = function () {
    //   // if (_this.scrollPage) {
    //   _this.mySwiper.autoplay.stop()
    //   _this.mySwiper.params.autoplay.delay = _this.scrollTime;
    //   _this.mySwiper.autoplay.start()
    //   // }

    // };
    // 鼠标离开 继续轮播
    //   this.mySwiper.el.onmouseleave = function () {
    //     if (_this.scrollPage) {
    //       _this.mySwiper.autoplay.stop()
    //       _this.mySwiper.params.autoplay.delay = _this.scrollTime
    //       _this.mySwiper.autoplay.start();
    //     }
    //     // _this.setSwiper()
    //   }
    // }, 2000)

    // console.log("Current Swiper instance object", this.swiper);
    // this.mySwiper.init();
    // mySwiper.el.style.opacity = 0.5;
  },
  methods: {
    setSwiper() {
      this.mySwiper = "";
      if (this.pageType == 1) {
        setTimeout(() => {
          this.mySwiper = new Swiper('.swiper-container', {
            direction: 'vertical',
            centeredSlides: true,
            autoplay: {
              delay: 2500,
              disableOnInteraction: false,
            },
            observer: true,
            observeParents: true,
            loop: true,
            observer: true,
            observeParents: true,
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            }
            // init: false
          })
          this.mySwiper.params.speed = this.speed
          this.mySwiper.params.autoplay.delay = this.scrollTime
          // if (!this.scrollPage) {
          this.mySwiper.autoplay.start();
          // }
        }, 0)
      } else if (this.pageType == 2) {
        setTimeout(() => {
          this.mySwiper = new Swiper('.swiper-container', {
            direction: 'horizontal',
            observer: true,
            observeParents: true,
            centeredSlides: true,
            autoplay: {
              delay: 2500,
              disableOnInteraction: false,
            },
            observer: true,
            observeParents: true,
            loop: true,
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            }
            // init: false
          })
          this.mySwiper.params.speed = this.speed
          this.mySwiper.params.autoplay.delay = this.scrollTime
          // if (!this.scrollPage) {
          this.mySwiper.autoplay.start();
          // }
        }, 0)
      } else if (this.pageType == 3) {
        setTimeout(() => {
          this.mySwiper = new Swiper('.swiper-container', {
            effect: 'flip',
            flipEffect: {
              slideShadows: true,
              limitRotation: true,
            },
            centeredSlides: true,
            autoplay: {
              delay: 2500,
              disableOnInteraction: false,
            },
            observer: true,
            observeParents: true,
            observer: true,
            observeParents: true,
            loop: true,
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            }
            // init: false
          })
          this.mySwiper.params.speed = this.speed
          this.mySwiper.params.autoplay.delay = this.scrollTime
          // if (!this.scrollPage) {
          this.mySwiper.autoplay.start();
          // }
        }, 0)
      } else if (this.pageType == 4) {
        setTimeout(() => {
          this.mySwiper = new Swiper('.swiper-container', {
            effect: 'cube',
            cubeEffect: {
              slideShadows: false,
              shadow: false,
              shadowOffset: 0,
              shadowScale: 0,
            },
            centeredSlides: true,
            autoplay: {
              delay: 2500,
              disableOnInteraction: false,
            },
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            },
            observer: true,
            observeParents: true,
            loop: true
            // init: false
          })
          this.mySwiper.params.speed = this.speed
          this.mySwiper.params.autoplay.delay = this.scrollTime
          // if (!this.scrollPage) {
          this.mySwiper.autoplay.start();
          // }
        }, 0)
      } else {
        setTimeout(() => {
          this.mySwiper = new Swiper('.swiper-container', {
            effect: 'fade',
            fadeEffect: {
              crossFade: true,
            },
            centeredSlides: true,
            autoplay: {
              delay: 2500,
              disableOnInteraction: false,
            },
            observer: true,
            observeParents: true,
            observer: true,
            observeParents: true,
            loop: true,
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            }
            // init: false
          })
          this.mySwiper.params.speed = this.speed
          this.mySwiper.params.autoplay.delay = this.scrollTime
          // if (!this.scrollPage) {
          this.mySwiper.autoplay.start();
          // }
        }, 0)
      }

      // this.mySwiper.autoplay.start();
    },
    allWidth() {
      // console.log('allWidth')
      // console.log(this.option.column)
      let num = 0
      if (this.rankBol) {
        num = num + parseInt(this.rankWidth)
      }
      for (var i = 0; i < this.option.column.length; i++) {
        if (this.option.column[i].width) {
          num = parseInt(num) + parseInt(this.option.column[i].width)
        } else {
          num = num + 200
        }
      }
      // console.log(num)
      return num + 'px'
    },
    rankw() {
      let obj = {
        width: this.rankWidth + 'px',
      }
      return obj
    },
    styleWidth(t) {
      let obj = {
        width: '200px',
      }
      for (var i = 0; i < this.option.column.length; i++) {
        if (
          t.prop === this.option.column[i].prop &&
          this.option.column[i].width
        ) {
          obj.width = this.option.column[i].width + 'px'
        }
      }
      return obj
    },
    group(array, subGroupLength) {
      let index = 0
      let newArray = []
      while (index < array.length) {
        newArray.push(array.slice(index, (index += subGroupLength)))
      }
      return newArray
    },
    // prev() {
    //   this.mySwiper.autoplay.stop()
    //   this.mySwiper.params.autoplay.delay = this.scrollTime;
    //   let nummmm = this.mySwiper.activeIndex - 1;
    //   this.mySwiper.slideTo(nummmm, 1000, false);
    //   // this.mySwiper.slidePrev(1000)
    //   // setTimeout(() => {
    //   //   this.mySwiper.autoplay.start()
    //   // }, 200)

    // },
    // next() {
    //   this.mySwiper.autoplay.stop()
    //   this.mySwiper.params.autoplay.delay = 10000;

    //   this.mySwiper.slideNext(1000)
    //   // let nummmm = this.mySwiper.activeIndex + 1;
    //   // this.mySwiper.slideTo(nummmm, 1000, false);
    //   // setTimeout(() => {
    //   //   this.mySwiper.autoplay.stop()
    //   //   this.mySwiper.params.autoplay.delay = this.scrollTime;
    //   this.mySwiper.autoplay.start()
    //   // }, 200)
    // },
    tableClick(item) {
      if (!!item && item.url) {
        window.parent.postMessage(item.url, "http://122.114.94.5:8080/");
      }
    }
    /**
    this.mySwiper.slideTo(0, 1000, false);
    this.topNum = 3;
    this.mySwiper.autoplay.stop();
      this.mySwiper.params.autoplay.delay = 1000
      this.mySwiper.params.speed = 2000
      this.mySwiper.changeDirection()
      this.mySwiper.autoplay.start()
    */
  },
}
</script>
