    height: auto;
    display: flex;
    flex-direction: column;

    &-title {
      display: inline-block;
    }

    &-rank {
      display: flex;
      flex-direction: column;

      // overflow: hidden;
      div {
        display: block;
      }
    }
  }
}

.swiper-container {
  float: left;
}

.swiper-wrapper {
  width: 100%;
  padding-bottom: 3px;
}

