  width: 100%;
}

.swiper-slide div {
  text-align: left;
  float: left;
  width: 100%;
}

.swiper-button-next,
.swiper-button-prev {
  background-size: 14px 22px;
  // width: 100px;
  // height: 30px;
  // background-image: none;
}
.swiper-button-prev:after,
.swiper-button-next:after {
  content: "";
}
.rankList {
  float: left;
  // min-width: 200px;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
  i {
    font-style: normal;
  }
}

.rankList span {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: #144280;
  border-radius: 30px;
  text-align: center;
  line-height: 30px;
  color: #fff;
}

.rankListTop1 span,
.rankList1 span {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: #fdd706;
  border-radius: 30px;
  text-align: center;
  line-height: 30px;
}
.rankListTop2 span,
.rankList2 span {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: #1f94f4;
  border-radius: 30px;
  text-align: center;
  line-height: 30px;
}
.rankListTop3 span,
.rankList3 span {
  display: inline-block;
