<template>
  <div :ref="id" :style="style">
    <div class="light" @click="clickArrow()" :style="style">
      <img :src="imgSrc" />
      <div class="lightbulb__glow" :style="glowStyle"></div>
    </div>
  </div>
</template>

<script>
import updateData from "@/mixins/updateData";
export default {
  name: "light",
  mixins: [updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      colorImgObj: {
        yellow: "light-yellow.svg",
        red: "light-red.svg",
        green: "light-green.svg"
      },
      lightGlowColor: {
        yellow: "rgb(255,251,119)",
        red: "rgb(249,70,47)",
        green: "rgb(73,209,58)"
      }
    };
  },
  computed: {
    minWidth() {
      const val = this.option.minWidth;
      if (val > this.width) return val;
    },
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    },
    lightColor() {
      return this.dataChart.value;
    },
    glowStyle() {
      let glowWidth = this.width > this.height ? this.height : this.width;
      return {
        width: this.setPx(glowWidth * 0.8),
        height: this.setPx(glowWidth * 0.8),
        filter: "blur(" + this.setPx(glowWidth * 0.2) + ")",
        background:
          "radial-gradient(" +
          this.lightGlowColor[this.lightColor] +
          "25%, transparent 80%)"
      };
    },
    imgSrc() {
      return `/img/light/${this.colorImgObj[this.lightColor]}`;
    }
  },
  methods: {
    clickArrow() {
      let url = this.dataChart.url;
      window.parent.postMessage(url, "http://122.114.94.5:8080/");
    }
  }
};
</script>

<style scoped>
.light {
  text-align: center;
  vertical-align: middle;
  position:relative;
}
.light img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

.lightbulb__glow {
  z-index: 8;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%) translateY(-15%) scale(0.5);
  border-radius: 50%;
  animation: glow 5s ease infinite;
}

@keyframes glow {
  0% {
    transform: translateX(-50%) translateY(-15%) scale(0.5);
  }

  50% {
    transform: translateX(-50%) translateY(-15%) scale(1.15);
  }

  100% {
    transform: translateX(-50%) translateY(-15%) scale(0.5);
  }
}
</style>