<script>
import updateData from "@/mixins/updateData";
export default {
  name: "beijing",
  mixins: [updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    component: {
      type: Object,
      default: () => {
        return {};
      }
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      currentPage: 1,
      blockNum: 14,
      myChart: "",
      checkSlider: "",
      idIndex: 0,
      sideColor: "",
      surfaceColor: ""
    };
  },
  computed: {
    fontSize() {
      return this.option.fontSize || 20;
    },
    fontColor() {
      return this.option.fontColor || '#fff';
    },
    banner() {
      return this.option.banner || false;
    },
    bannerTime() {
      return +this.option.bannerTime || 0;
    },
    dataArr() {
      let startIndex = (this.currentPage - 1) * this.blockNum;
      return this.dataChart.slice(startIndex, startIndex + this.blockNum) || [];
    },
    minWidth() {
      const val = this.option.minWidth;
      if (val > this.width) return val;
    },
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    },
    textStyle(){
      return {
        fontSize:this.setPx(this.fontSize),
        fill:this.fontColor
      }
    },
    pageNum() {
      return Math.ceil(this.dataChart.length / 10);
    }
  },
  mounted() {
    this.addEventListener();
  },
  watch: {
    banner: {
      handler(newVal, oldVal) {
        this.slide();
      },
      deep: true,
      immediate: true
    },
    bannerTime: {
      handler(val) {
        this.slide();
      },
      deep: true,
      immediate: true
    },
    option:{
      handler(){
        this.myChart && this.updateChart();
      },
      immediate: true,
      deep:true
    }
  },
  methods: {
    addEventListener() {
      let d3 = window.d3;
      let that = this;
      d3.select("div.beijing svg")
        .selectAll("g.module")
        .attr("cursor", "pointer")
        .on("mouseover", function() {
          d3.select(this)
            .transition()
            .duration(100)
            .ease(d3.easeLinear)
            .attr("transform", "translate(0,-10)");
          d3.select(this)
            .selectAll(".side")
            .classed("sideColor", true);
          d3.select(this)
            .select(".surface")
            .classed("surfaceColor", true);
        })
        .on("mouseout", function() {
          d3.select(this)
            .transition()
            .duration(100)
            .ease(d3.easeLinear)
            .attr("transform", "translate(0,0)");
          d3.select(this)
            .select(".surface")
            .classed("surfaceColor", false);
          d3.select(this)
            .selectAll(".side")
            .classed("sideColor", false);
        })
        .on("click",function(){
          let id = this.id.slice(1);
          let paramsData = that.dataArr[id] || '';
          window.parent.postMessage(paramsData, "http://122.114.94.5:8080/");
        })
    },
    changeLeft() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    changeRight() {
      if (this.currentPage < this.pageNum) {
        this.currentPage++;
      }
    },
    slide() {
      let index = 0;
      let d3 = window.d3;
      this.$nextTick(() => {
        this.checkSlider && this.checkSlider.stop()
        this.resetChart();
        if (this.bannerTime !== 0 && this.banner) {
          this.checkSlider = d3.interval(() => {
            this.setSlide(index);
            index++;
            if (index === this.blockNum) {
              index = 0;
            }
          }, this.bannerTime);
        }
      });
    },
    setSlide(idIndex) {
      let d3 = window.d3;
      let logoSvg = d3.select("div.beijing svg");
      this.resetChart();
      logoSvg
        .select("#b" + idIndex)
        .transition()
        .duration(this.bannerTime / 2)
        .ease(d3.easeLinear)
        .style("fill", "#2563c6")
        .attr("transform", "translate(0,-10)");
      logoSvg.select("#b" + idIndex).selectAll(".side").classed("sideColor", true);
      logoSvg.select("#b" + idIndex).select(".surface").classed("surfaceColor", true);
    },
    resetChart(){
      let logoSvg = d3.select("div.beijing svg");
      logoSvg
        .selectAll("g.module")
        .transition()
        .duration(this.bannerTime/2)
        .ease(d3.easeLinear)
        .attr("transform", "translate(0,0)")
      logoSvg.selectAll("g.module").selectAll(".side").classed("sideColor", false);
      logoSvg.selectAll("g.module").select(".surface").classed("surfaceColor", false);   

    }
  }
};
</script>

