<template>
  <div :ref="id" :style="style"></div>
</template>


<script>
import color from "@/mixins/color";
import updateData from "@/mixins/updateData";
export default {
  name: "circleUpgrade",
  mixins: [color, updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    }
  },
  watch: {
    style() {
      this.$nextTick(() => {
        this.myChart && this.myChart.resize();
      });
    },
    option:{
      handler(){
        this.myChart && this.updateChart();
      },
      immediate: true,
      deep:true
    }
  },
  methods: {
    init() {
      // 判断是否引入echart包
      if (!window.echarts) {
        console.log("未引入echarts！");
        return;
      }
      const main = this.$refs[this.id];
      if (main) {
        this.myChart = window.echarts.init(main, this.theme);
      }
    },
    updateChart() {
      this.myChart.clear();
      const optionData = this.deepClone(this.dataChart);
      const option = {
        title: {
        text: `${optionData.value + optionData.unit}`,
        subtext: optionData.name,
        left: 'center',
        top: 'center', //top待调整
        textStyle: {
            color: '#fff',
            fontSize: this.option.fontSize,
            fontFamily: 'DINAlternate-Bold',
        },
        subtextStyle: {
            color: '#ff',
            fontSize: this.option.subFontSize,
            fontFamily: 'PingFangSC-Regular',
            top: 'center'
        },
        itemGap: -4 //主副标题间距
    },
    xAxis: {
        splitLine: {
            show: false
        },
        axisLabel: {
            show: false
        },
        axisLine: {
            show: false
        }
    },
    yAxis: {
        splitLine: {
            show: false
        },
        axisLabel: {
            show: false
        },
        axisLine: {
            show: false
        }
    },
    series: [
        // 内圆
        {
            type: 'pie',
            radius: ['0', '50%'],
            center: ['50%', '50%'],
            z: 0,
            itemStyle: {
                normal: {
                    color: this.option.circleColor,
                    label: {
                        show: false
                    },
                    labelLine: {
                        show: false
                    }
                },
            },
            label: {
                normal: {
                    position: "center",

                }
            },
            data: [100],
        },
        // 进度圈
        {
            type: 'pie',
            clockWise: true,
            radius: ["60%", "55%"],
            data: [{
                    value: optionData.value,
                    itemStyle: {
                        normal: {
                            borderWidth: 10,
                            borderColor: this.option.barColor,
                            color: this.option.barColor,
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            },
                        },
                    }
                },
                {
                    name: 'gap',
                    value: 100 - optionData.value,
                    itemStyle: {
                        normal: {
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            },
                            color: 'rgba(0, 0, 0, 0)',
                            borderColor: 'rgba(0, 0, 0, 0)',
                            borderWidth: 0,
                        }
                    },
                }
            ]
        },
        //刻度尺
        {
            // name: "白色圈刻度",
            type: "gauge",
            radius: "67%",
            startAngle: 225, //刻度起始
            endAngle: -134.8, //刻度结束
            z: 4,
            axisTick: {
                show: true,
                 lineStyle: {
                    width: 2,
                    color: this.option.pointColor
                } 
            },
            splitLine: {
                length: 16, //刻度节点线长度
                lineStyle: {
                    width: 2,
                    color: this.option.pointColor
                } //刻度节点线
            },
            axisLabel: {
                color: 'rgba(255,255,255,0)',
                fontSize: 12,
            }, //刻度节点文字颜色
            pointer: {
                show: false
            },
            axisLine: {
                lineStyle: {
                    opacity: 0
                }
            },
            detail: {
                show: false
            },
            data: [{
                value: 0,
                name: ""
            }]
        },
        // 刻度圈
        {
            "type": "pie",
            "radius": ["0%", "73%"],
            "center": ["50%", "50%"],
            "avoidLabelOverlap": false,
            "label": {
                "normal": {
                    "show": false,
                    "position": "center"
                },
                "emphasis": {
                    "show": false,
                    "textStyle": {
                        "fontWeight": "bold"
                    }
                }
            },
            "itemStyle": {
                "normal": {
                    "color": {
                        "type": "linear",
                        "x": 0,
                        "y": 0,
                        "x2": 0,
                        "y2": 1,
                        "colorStops": [{
                            "offset": 0.05,
                            "color": "rgba(32,36,107, 0.2)"
                        }, {
                            "offset": 0.5,
                            "color": "rgba(32,36,107,0.3)"
                        }, {
                            "offset": 0.95,
                            "color": "rgba(32,36,107, 0.2)"
                        }]
                    }
                }
            },
            "labelLine": {
                "normal": {
                    "show": false
                }
            },
            "data": [{
                "value": 3235
            }]
        },
        //最外层圈
        {
            "type": "pie",
            "radius": ["0%", "85%"],
            "center": ["50%", "50%"],
            "avoidLabelOverlap": false,
            hoverAnimation: false,
            "label": {
                "normal": {
                    "show": false,
                    "position": "center"
                },
                "emphasis": {
                    "show": false,
                    "textStyle": {
                        "fontWeight": "bold"
                    }
                }
            },
            "itemStyle": {
                "normal": {
                    "color": {
                        "type": "linear",
                        "x": 0,
                        "y": 0,
                        "x2": 0,
                        "y2": 1,
                        "colorStops": [{
                            "offset": 0.05,
                            "color": "rgba(21,24,65, 0.2)"
                        }, {
                            "offset": 0.5,
                            "color": "rgba(21,24,65, 0.3)"
                        }, {
                            "offset": 0.95,
                            "color": "rgba(21,24,65, 0.2)"
                        }]
                    }
                }
            },
            "labelLine": {
                "normal": {
                    "show": false
                }
            },
            "data": [{
                "value": 3235
            }]
        }
    ]
        
      };
      this.myChart.resize();
      this.myChart.setOption(option);
    }
  }
};
</script>

