<template>
  <div :ref="id" :style="style"></div>
</template>


<script>
import color from "@/mixins/color";
import updateData from "@/mixins/updateData";
export default {
  name: "circularRotate",
  mixins: [color, updateData],
  props: {
    width: {
      type: [Number, String],
      default: 600
    },
    height: {
      type: [Number, String],
      default: 600
    },
    id: {
      type: String,
      default: "main_" + new Date().getTime()
    },
    option: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      timer: "",
      pieData: {
        value: 0,
        startAngle: 90,
        startAngle2: 50
      }
    };
  },
  mounted() {
    this.init();
    this.slide();
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  computed: {
    style() {
      return {
        width: this.setPx(this.minWidth || this.width),
        height: this.setPx(this.height)
      };
    }
  },
  watch: {
    style() {
      this.$nextTick(() => {
        this.myChart && this.myChart.resize();
      });
    },
    option:{
      handler(){
        this.myChart && this.updateChart();
      },
      immediate: true,
      deep:true
    }
  },
  methods: {
    init() {
      // 判断是否引入echart包
      if (!window.echarts) {
        console.log("未引入echarts！");
        return;
      }
      const main = this.$refs[this.id];
      if (main) {
        this.myChart = window.echarts.init(main, this.theme);
      }
    },
    updateChart() {
      this.myChart.clear();

      const option = {
        series: [
          {
            type: "pie",
            zlevel: 1,
            silent: true,
            /*
            radius
            饼图的半径。可以为如下类型：
            number：直接指定外半径值。
            string：例如，'20%'，表示外半径为可视区尺寸（容器高宽中较小一项）的 20% 长度。
            Array.<number|string>：数组的第一项是内半径，第二项是外半径。每一项遵从上述 number string 的描述。
            */
            radius: ["98%", "97%"],
            hoverAnimation: false,
            color: "rgba(88,142,197,0.5)",
            animation:false,    //charts3 no
            label: {
              normal: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [1]
          },
          {
            type: "pie",
            zlevel: 2,
            silent: true,
            radius: ["90%", "91%"],
            startAngle: 50,
            hoverAnimation: false,
            animation:false, 
            label: {
              normal: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this._pie2()
          },
          {
            type: "pie",
            zlevel: 3,
            silent: true,
            animation:false, 
            radius: ["88%", "87%"],
            label: {
              normal: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this._pie2()
          },
          {
            type: "pie",
            zlevel: 4,
            silent: true,
            radius: ["84%", "83%"],
            startAngle: this.pieData.startAngle,
            animation:false, 
            label: {
              normal: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this._pie3()
          },
          {
            type: "pie",
            zlevel: 5,
            silent: true,
            radius: ["80%", "78%"],
            color: ["#fc8d89", "#46d3f3", "rgba(203,203,203,.2)"],
            startAngle: this.pieData.startAngle2,
            animation:false, 
            hoverAnimation: false,
            label: {
              normal: {
                show: false
              }
            },
            data: [50, 20, 40]
          },
          {
            name: "",
            type: "gauge",
            splitNumber: 30, //刻度数量
            min: 0,
            max: 100,
            radius: "73%", //图表尺寸
            center: ["50%", "50%"],
            startAngle: 90,
            endAngle: -269.9999,
            axisLine: {
              show: false,
              lineStyle: {
                width: 0,
                shadowBlur: 0,
                color: [[1, "#0dc2fe"]]
              }
            },
            axisTick: {
              show: false,
              lineStyle: {
                color: "auto",
                width: 2
              },
              length: 20,
              splitNumber: 5
            },
            splitLine: {
              show: true,
              length: 32,
              lineStyle: {
                color: "auto"
              }
            },
            axisLabel: {
              show: false
            },
            pointer: {
              //仪表盘指针
              show: 0
            },
            detail: {
              show: 0
            }
          },
          {
            name: "统计",
            type: "gauge",
            splitNumber: 30, //刻度数量
            min: 0,
            max: 100,
            radius: "68%", //图表尺寸
            center: ["50%", "50%"],
            startAngle: 90,
            endAngle: -269.9999,
            axisLine: {
              show: true,
              lineStyle: {
                width: 0,
                shadowBlur: 0,
                color: [
                  [0, "#0dc2fe"],
                  [1, "#0dc2fe"]
                ]
              }
            },
            axisTick: {
              show: true,
              lineStyle: {
                color: "#0dc2fe",
                width: 2
              },
              length: 20,
              splitNumber: 5
            },
            splitLine: {
              show: true,
              length: 20,
              lineStyle: {
                color: "#0dc2fe"
              }
            },
            axisLabel: {
              show: false
            },
            pointer: {
              //仪表盘指针
              show: 0
            },
            detail: {
              borderColor: "#fff",
              shadowColor: "#fff", //默认透明
              shadowBlur: 2,
              offsetCenter: [0, "0%"], // x, y，单位px
              textStyle: {
                // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                color: "#fff",
                fontSize: 50
              },
              formatter: "{value}"
            },
            data: [
              {
                name: "",
                value: this.pieData.value
              }
            ]
          },
          {
            type: "pie",
            zlevel: 20,
            silent: true,
            radius: ["60%", "59%"],
            hoverAnimation: false,
            animation:false, 
            color: "#2dc0c9",
            // animation:false,
            data: [1],
            labelLine: {
              normal: {
                show: false
              }
            }
          },
          {
            name: "中间环形图",
            type: "pie",
            radius: ["35%", "55%"],
            avoidLabelOverlap: false,
            hoverAnimation: false,
            animation:false, 
            itemStyle: {
              normal: {
                color: "#80ADD2",
                borderColor: "#3D4268"
              }
            },
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [25, 25, 25, 25, 25, 25]
          }
        ]
      };
      this.myChart.resize();
      this.myChart.setOption(option);
    },
    _pie2() {
      let dataArr = [];
      for (var i = 0; i < 8; i++) {
        if (i % 2 === 0) {
          dataArr.push({
            name: (i + 1).toString(),
            value: 25,
            itemStyle: {
              normal: {
                color: "rgba(88,142,197,0.5)",
                borderWidth: 0,
                borderColor: "rgba(0,0,0,0)"
              }
            }
          });
        } else {
          dataArr.push({
            name: (i + 1).toString(),
            value: 20,
            itemStyle: {
              normal: {
                color: "rgba(0,0,0,0)",
                borderWidth: 0,
                borderColor: "rgba(0,0,0,0)"
              }
            }
          });
        }
      }
      return dataArr;
    },
    _pie3() {
      let dataArr = [];
      for (var i = 0; i < 100; i++) {
        if (i % 2 === 0) {
          dataArr.push({
            name: (i + 1).toString(),
            value: 25,
            itemStyle: {
              normal: {
                color: "rgb(126,190,255)",
                borderWidth: 0,
                borderColor: "rgba(0,0,0,0)"
              }
            }
          });
        } else {
          dataArr.push({
            name: (i + 1).toString(),
            value: 20,
            itemStyle: {
              normal: {
                color: "rgba(0,0,0,0)",
                borderWidth: 0,
                borderColor: "rgba(0,0,0,0)"
              }
            }
          });
        }
      }
      return dataArr;
    },
    slide() {
      let index = 0;
      this.$nextTick(() => {
        this.timer = setInterval(() => {
          this.pieData.startAngle --;
          this.pieData.startAngle2 ++;
          this.pieData.value ++;
          this.updateChart();
          
          if (this.pieData.value== this.dataChart.value) {
            clearInterval(this.timer);
          }
        }, 100);
      });
    }
  }
};
</script>

<style>
</style>